.tabs {
  min-width: 320px;
  width: 100%;
  padding: 0px;
  margin: 0 auto 0 0;
  position: relative;

  &> section {
    display: none;
    background: #fff;
    border: 1px solid #ddd;

    &> .flex {
      margin: 0 0 5px;
      line-height: 1.5;
      color: #383838;
      animation-duration: .5s;
      animation-fill-mode: both;
      animation-name: fadeIn; } }

  &> input {
    display: none;
    position: absolute; }

  &> input:checked + label {
    color: #555;
    border-top: 1px solid #ffa500;
    border-bottom: 1px solid #fff;
    background: #fff; }

  &> label {
    display: inline-block;
    margin: 0 0 -1px;
    padding: 9px 3px;
    font-weight: 600;
    text-align: center;
    border: 1px solid #ddd;
    background: #f1f1f1;
    border-radius: 3px 3px 0 0;

    @media screen and (max-width: 400px) {
      padding: 15px; }

    @media screen and (max-width: 680px) {
      font-size: 7px; } }

  &> label:before {
    font-weight: normal;
    margin-right: 10px;
    @media screen and (max-width: 680px) {
      margin: 0;
      font-size: 18px; } }

  &> label:hover {
    color: #888;
    cursor: pointer; } }

/* Активация секций с помощью псевдокласса :checked */
#tab1:checked ~ #content-tab1,
#tab2:checked ~ #content-tab2,
#tab3:checked ~ #content-tab3,
#tab4:checked ~ #content-tab4 {
  display: flex;
  flex-direction: column;
  padding: 15px;
  min-height: 100vh; }

#content-tab1 strong {
  color: #4d4d4d; }

