.has-children {

  label {
    display: flex; }

  &> label a:before {
    content: '';
    border: 4px solid;
    border-color: transparent transparent transparent #ccc;
    position: absolute;
    right: -16px;
    top: 3px; } }


.menu {
  width: 130px;
  background: #001a98;
  align-self: flex-start;

  label, a, li {
    position: relative;
    display: block;
    color: #c1c1c1;
    font-size: 12px; }

  input:checked + label > a {
    font-size: 13px;
    font-weight: 600;
    color: #fff;
    text-transform: uppercase; }

  &> li {
    font-size: 13px; }

  li ul li:hover,
  li ul li a:hover {
    /*padding: 2px*/
    font-size: 13px;
    color: #ffa500;
    font-weight: 600; }

  ul {
    display: none;
    cursor: pointer; }

  .drop {
    background: #001a98;
    cursor: pointer;
    padding: 9px 0 9px 9px;

    li {
      padding: 0 5px; } }

  &> li:hover,
  &> label a:hover,
  li:hover > li label {
    background: #5e5e5e; }

  &> li:not(.has-children) {
    padding: 9px 0 9px 0px; }

  &> li > label {
    padding: 9px 0 9px 9px; }

  input[type=checkbox]:checked + label + ul,
  input[type=checkbox]:checked + label:nth-of-type(n) + ul {
    display: block; }

  input[type=checkbox] {
    position: absolute;
    opacity: 0; } }







