/* *///////////////////////*/
/* *///////  A CATALOG-MENU  ////////*/
/* *///////////////////////*/

.admin-category-menu {
  flex: 1;
  z-index: 1;
  max-width: 350px;

  a {
    display: flex;
    align-items: center;
    flex: 1;
    padding: 7px 14px 7px 7px;
    transition: 0.5s all;
    font-size: 0.65rem;
    font-weight: bold;
    text-transform: uppercase;
    min-width: 40px;
    max-width: 190px;
    background-color: rgba(229, 237, 239, 0.73); }

  li {
    display: flex;
    position: relative;
    border-bottom: 1px solid #b7b7b7;
    -webkit-transition: 0.5s all;
    transition: 0.5s all;
    justify-content: flex-end;

    .test-link a {
      text-transform: uppercase; } }


  .vert-menu__list /*Иконки для всплывающих менюшек*/ {
    &:after {
      content: " ";
      position: absolute;
      border: .6vw solid;
      border-color: transparent transparent transparent #84989a;
      transition: 0.5s;
      align-self: center;

      @media screen and (min-width: 1200px) {
        border: .4vw solid;
        border-color: transparent transparent transparent #eee; } }

    &:hover:after {
      transform: scaleX(-1); }

    &:hover > .vert-menu__drop {
      opacity: 1;
      top: 0;
      z-index: 1; } }

  .vert-menu__drop /*Всплывающие меню*/ {
    position: absolute;
    width: 100%;
    left: 100%;
    top: -9999em;
    opacity: 0;
    border-left: 1px solid transparent;
    -webkit-transition: 0.5s opacity;
    transition: 0.5s opacity; } }



